  position: relative;
  margin-bottom: 5px;
  margin-top: 5px;
  display: inline-block;
  width: 100%;
}
label:not(:has(input[type=radio], input[type=checkbox])) {
  font-size: 1.2rem;
}
label input:not([type=radio], [type=checkbox]),
label textarea {
  width: 100%;
}
label .text {
  position: absolute;
  z-index: 1;
  transition: all 0.5s;
  transform: translate(8px, 14px);
  user-select: none;
  cursor: text;
}
