  transform: translate(0px, -7px);
  font-size: 0.75rem;
  font-weight: bold;
}
label .password-container {
  position: relative;
}
label .password-container .password-toggle {
  width: 24px;
  position: absolute;
  top: 27px;
  translate: -100% -50%;
  right: 0;
  scale: 0.8;
  cursor: pointer;
  font-size: 12px;
}
label .static-label-text {
  font-size: 0.75rem;
  white-space: nowrap;
  margin-top: 4px;
}
