  translate: -44px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
label[for=remember-me] input {
  scale: 1.3;
  translate: 5px -5px;
  transform-origin: top left;
  cursor: pointer;
}
label input,
label textarea {
  margin: 10px 0 0 0;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
label textarea {
  resize: vertical;
  min-height: 100px;
}
label .hide-password {
  display: none;
}
label .show-password {
  display: block;
}
