  margin: 10px;
}
label .images-container.file-input-display .ifEmpty {
  margin: auto;
}
label .images-container.file-input-display.has-images .ifEmpty {
  display: none;
}

.file-input-component {
  width: 220px;
}
.file-input-component .img-container:not(:empty) {
  border: 1px solid black;
}
.file-input-component .label-container {
  position: relative;
  width: 200px;
  height: 200px;
  overflow: visible;
  margin: auto;
}
.file-input-component .delete-button {
  position: absolute;
  top: 0;
  right: 0;
  background-color: red;
  color: white;
  border: none;
  padding: 5px;
  cursor: pointer;
}
.file-input-component .file-name,
.file-input-component .error {
  text-align: center;
}
.file-input-component .error {
  border: 1px solid black;
  color: red;
  font-weight: bold;
}

.file-input-component .alert-icon {
  display: none;
}
.file-input-component .ifLoading {
  display: none;
}
.file-input-component.loading .ifLoading {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.file-input-component.loading .ifEmpty {
  display: none;
}
.file-input-component.hasError .alert-icon {
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 60px;
  color: #ff0000;
}

.password-toggle {
  position: absolute;
  top: 27px;
  translate: 0% -50%;
  right: 10px;
  scale: 0.8;
  cursor: pointer;
